.lb-next, .lb-prev {
	height: 100%;
	cursor: pointer;
	display: block
}
.lb-nav a.lb-prev {
	width: 49px;
	left: 0;
	float: left;
	/* background: url(../images/sprit-icons4963.png?ver=1.1) -69px 50% no-repeat; */
	filter: alpha(Opacity=0);
	opacity: 0;
	-webkit-transition: opacity .6s;
	transition: opacity .6s
