	filter: alpha(Opacity=100);
	opacity: 1
}
.lb-nav a.lb-next {
	width: 49px;
	right: 0;
	float: right;
	/* background: url(../images/sprit-icons4963.png?ver=1.1) -29px 50% no-repeat; */
	filter: alpha(Opacity=0);
	opacity: 0;
	-webkit-transition: opacity .6s;
	transition: opacity .6s
}
