#PostFGrid select {
  height: 3.4em;
    line-height: 2;
    background: #ffffff;
    color: #dc1e28;
    border: 1px solid #dc1e28;
}
#PostFGrid select:first-of-type {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
#PostFGrid select:last-of-type {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
#PostFGrid select:focus,
#PostFGrid select:active {
  outline: 0;
}
#PostFGrid select option {
  color: #333;
}

#PostFGrid select {
  display: inline-block;
  text-align: center;
  float: left;
  border-radius: 0;
  cursor:pointer;
}


#PostFGrid input[type="radio"] {
  display: none;
}
#PostFGrid input[type="radio"] + label
 {
  display: inline-block;
  text-align: center;
  float: left;
  border-radius: 0;
  cursor:pointer;
   text-transform:uppercase;
}
#PostFGrid input[type="radio"] + label:first-of-type {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
#PostFGrid input[type="radio"] + label:last-of-type {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
#PostFGrid input[type="radio"] + label i {
  padding-right: 0.4em;
}

#PostFGrid input[type="radio"]:checked + label,
#PostFGrid input:checked + label:before,
#PostFGrid select:active {
  color: #e61717;
  border-color: #e61717;
}
#PostFGrid input[type="checkbox"] {
  display: none;
}
#PostFGrid input[type="checkbox"] + label {
  position: relative;
  display: block;
  padding-left: 2.6em;
  margin-left: 16px;
}
#PostFGrid input[type="checkbox"] + label:before {
    position: absolute;
    top: 0.2em;
    left: 0;
    display: block;
    width: 1.6em;
    height: 1.6em;
    padding: 0;
    content: "";
    border: 1px solid #8c0007;
    background: #dc1e28;
}
#PostFGrid input[type="checkbox"] + label:after {
  position: absolute;
  top: 0.5em;
  left: 0.5em;
  font-size: 0.8em;
  color: #fff;
  opacity: 0;
  font-family: FontAwesome;
  content: "\f00c";
}
#PostFGrid input:checked + label:after {
  opacity: 1;
}


